import React from "react";

export function Example(props) {
  let classes = "example";
  classes = props.animate ? classes + " fragment" : classes;

  return (
    <div {...props} className={classes}>
      {props.children}
    </div>
  );
}

export function Tooltip(props) {
  let classes = "tooltip";
  classes = props.animate ? classes + " fragment" : classes;

  const bulb =
    "data:image/svg+xml;base64,";
  return (
    <div {...props} className={classes}>
      <img src={bulb} />
      <div>
        <div className="tooltip-title">¡Ojo!</div>
        {props.children}
      </div>
    </div>
  );
}

export function Definition(props) {
  let classes = "definition";

  classes = props.animate ? classes + " fragment" : classes;

  return (
    <div {...props} className={classes}>
      <span className="definitionWord">{props.word}:</span> {props.children}
    </div>
  );
}
